

$dark_cb:          #081111;
$dark_bg:          #0A1616;
$dark_hl_bg:       #010303;
$dark_fg:          #EAF7F7;
$dark_dim_fg:      #61B3B3;
$dark_stroke:      #3B7878;
$dark_accent:      #C5FFFF;
$dark_accent_nd:   #8BFFFF;
$dark_sidebar_bg:  #112222;
$dark_sidebar_fg:  #EAF7F7;
$dark_admonition_note:      #8bb158;
$dark_admonition_tip:       #dddddd;
$dark_admonition_warning:   #bf6900;
$dark_admonition_caution:   #bf3400;
$dark_admonition_important: #bf0000;

$light_cb:         #ffffff;
$light_bg:         #f5f5f5;
$light_hl_bg:      #e6e6e6;
$light_fg:         #262625;
$light_dim_fg:     #40403e;
$light_stroke:     #575754;
$light_accent:     #ab5b00;
$light_accent_nd:  #465a2a;
$light_sidebar_bg: #e6e6e6;
$light_sidebar_fg: #121211;
$light_admonition_note:      #19407c;
$light_admonition_tip:       #111111;
$light_admonition_warning:   #bf6900;
$light_admonition_caution:   #bf3400;
$light_admonition_important: #bf0000;

:root {
    --dark_bg:          #{$dark_bg};
    --dark_cb:          #{$dark_cb};
    --dark_hl_bg:       #{$dark_hl_bg};
    --dark_fg:          #{$dark_fg};
    --dark_dim_fg:      #{$dark_dim_fg};
    --dark_stroke:      #{$dark_stroke};
    --dark_accent:      #{$dark_accent};
    --dark_accent_nd:   #{$dark_accent_nd};
    --dark_sidebar_bg:  #{$dark_sidebar_bg};
    --dark_sidebar_fg:  #{$dark_sidebar_fg};
    --dark_admonition_tip: #{$dark_admonition_tip};
    --dark_admonition_note: #{$dark_admonition_note};
    --dark_admonition_warning: #{$dark_admonition_warning};
    --dark_admonition_caution: #{$dark_admonition_caution};
    --dark_admonition_important: #{$dark_admonition_important};

    --light_bg:         #{$light_bg};
    --light_cb:         #{$light_cb};
    --light_hl_bg:      #{$light_hl_bg};
    --light_fg:         #{$light_fg};
    --light_dim_fg:     #{$light_dim_fg};
    --light_stroke:     #{$light_stroke};
    --light_accent_nd:  #{$light_accent_nd};
    --light_sidebar_bg: #{$light_sidebar_bg};
    --light_sidebar_fg: #{$light_sidebar_fg};
    --light_admonition_tip: #{$light_admonition_tip};
    --light_admonition_note: #{$light_admonition_note};
    --light_admonition_warning: #{$light_admonition_warning};
    --light_admonition_caution: #{$light_admonition_caution};
    --light_admonition_important: #{$light_admonition_important};

    
        --default_bg:         #{$dark_bg};
        --default_cb:         #{$dark_cb};
        --default_hl_bg:      #{$dark_hl_bg};
        --default_fg:         #{$dark_fg};
        --default_dim_fg:     #{$dark_dim_fg};
        --default_stroke:     #{$dark_stroke};
        --default_accent:     #{$dark_accent};
        --default_accent_nd:  #{$dark_accent_nd};
        --default_sidebar_bg: #{$dark_sidebar_bg};
        --default_sidebar_fg: #{$dark_sidebar_fg};
        --default_admonition_tip: #{$dark_admonition_tip};
        --default_admonition_note: #{$dark_admonition_note};
        --default_admonition_warning: #{$dark_admonition_warning};
        --default_admonition_caution: #{$dark_admonition_caution};
        --default_admonition_important: #{$dark_admonition_important};
    
}




$main_font: Clear Sans;
$title_font: Ubuntu;
$mono_font: Noto Sans Mono;
$extra_mono_font: Computer Modern Concrete;
$symbols_font: "Symbols Nerd Font";
$content_width: 1500px;
$border_radius: 10px;
$margin: 15px;

$base_font_size: 16px;
$title_font_mult: 1.2;
$main_font_mult:  1.1;
$mono_font_mult:  1.3;


    $shadow: 0 0 5px 0 rgba(0, 0, 0, .2);


::selection, ::-moz-selection {
    background: var(--default_accent);
    color: var(--default_fg);
}

body, html {
    font-family: $symbols_font, $main_font, sans-serif;
    font-size: $base_font_size * $main_font_mult;
    margin: 0;
    padding: 0;
    background-color: var(--default_bg);
    color: var(--default_fg);
}

body {
    min-height: 100vh;
}

@mixin linkLike {
    color: var(--default_accent);
    text-decoration: none;
    background-color: transparent;
    border-radius: $border_radius;
    border-bottom: 0px solid currentcolor; /* link underline */
    padding: ($margin / 2);
    margin: ($margin / 6) 0;
    transition: background-color .15s ease-in-out !important;
    &:hover, &:focus {
        &, &:not([disabled]) {
            border-color: var(--default_accent) !important;
            background-color: var(--default_accent);
            color: var(--default_bg);
        }
    }
}

@mixin roundButtonLike {
    display: inline-block;
    border-radius: $border_radius;
    background-color: var(--default_hl_bg);
    color: var(--default_fg);
    padding: ($margin / 2);
    margin: ($margin / 6) 0;
    transition: background-color .15s ease-in-out !important;
    border-bottom: none;
    &:hover, &:focus {
        background-color: var(--default_accent);
    }
    box-shadow: $shadow;
}

@mixin buttonLike {
    
        @include linkLike;
    
    font-size: #{1.2 * $base_font_size};
}

a {
    @include linkLike;
    /* font-family: $symbols_font, $extra_mono_font, serif; */
}

a.unstyledLink {
    border: none;
    text-decoration: none;
    color: var(--default_fg);
}

@mixin limitWidth {
    max-width: $content_width;
    margin-left: auto;
    margin-right: auto;
}

#content {
    @include limitWidth;
    padding: $margin;
    pre {
        font-family: $symbols_font, $mono_font, monospace;
    }
}

footer {
    color: var(--default_dim_fg);
    a {
        color: var(--default_dim_fg);
    }
    hr {
        margin-top: (2 * $margin);
    }
    .footerColumns {
        font-size: .9 * $base_font_size * $main_font_mult;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        ul {
            margin: ($margin / 2);
            list-style-type: none;
            padding: 0;
            li > strong {
                font-size: 1rem * $main_font_mult;
            }
        }
    }
}

#links {
    padding: 5px 0;
}

article, .articlePreview {
    img {
        margin: auto;
        display: block;
        max-width: 100%;
    }

    pre, code {
        overflow: auto;
    }
    pre {
        padding: $margin;
        border: 1px solid var(--default_stroke);
        border-radius: 3px;
    }
}

p {
    code {
        font-family: $symbols_font, $mono_font, monospace;
        font-size: $base_font_size * .75 * $mono_font_mult;
        /* border: 1px solid var(--default_stroke); */
        border-radius: 7px;
        padding: 5px;
        color: var(--default_fg);
        background-color: var(--default_cb);
        /* background-color: #ff7700; */
    }
}

.listingblock, .exampleblock, .sidebarblock, .paragraph {
    .title {
        font-family: $symbols_font, $title_font, serif;
        /* color: var(--default_dim_fg); */
        font-weight: bold;
        /* color: var(--default_accent_nd); */
    }
}

*::selection {
    background: var(--default_accent_nd);
    color: var(--default_bg);
}

code {
    font-family: $symbols_font, $mono_font, monospace;
    font-size: $base_font_size * .8 * $mono_font_mult;
    border-radius: 3px;
    border: 1px solid var(--default_sidebar_bg);
}
code, table {
    padding: 2px;
}

.rouge {
    border: 1px solid var(--default_dim_fg);
    background-color: 1px solid var(--default_cb);
}

.literalblock {
    .content {
        pre {
            border: 1px solid var(--default_sidebar_bg);
        }
    }
}

/* .exampleblock { */
/*     .content { */
/*         border: 1px solid var(--default_accent); */
/*         padding: 20px; */
/*     } */
/* } */

pre code {
    border: none;
    border-radius: none;
    padding: 0;
}

table {
    border-collapse: collapse;
    /* overflow-x: auto; */
    display: block;
    td, th {
        padding: 6px;
        border: 1px solid var(--default_stroke);
    }
    th, tr:nth-child(even) {
        filter: brightness(105%);
    }
    th {
        font-weight: bold;
    }
    overflow: auto;
}

blockquote {
    margin: 0;
    padding-left: ($margin * 2);
    border-left: 6px solid var(--default_dim_fg);
}

.quoteblock {
    .title {
        padding-top: ($margin * 1.5);
        font-size: $base_font_size * 1 * $mono_font_mult;
        /* color: var(--default_accent_nd); */
        /* font-weight: bold; */
    }
}

.date, .date a {
    color: var(--default_dim_fg);
    border-color: var(--default_dim_fg);
    font-size: .9em;
    font-family: $symbols_font, $mono_font, monospace;
}

#pageNavigation, .nerdlink, nav:not(#TableOfContents) {
    a {
        border-bottom: none;
        &:hover {
            border-bottom: none;
        }
    }
}

#pageNavigation {
    text-align: right;
    a {
        color: var(--default_accent);
    }
    a, span {
        // appropriately sized tap targets
        display: inline-block;
        padding: 6px;
    }
    &.nav-circles {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-end;
        align-items: center;
        a, span {
            margin: 5px;
            text-align: center;
            display: inline-block;
            width: 40px;
            height: 40px;
            line-height: 40px;
            padding: unset;
            border-radius: 100%;
            transition: background-color .15s ease-in-out;
            background-color: var(--default_hl_bg);
            color: var(--default_fg);
            box-shadow: $shadow;
        }
        span {
            background-color: var(--default_accent);
        }
        a:hover {
            background-color: var(--default_accent);
        }
    }
}

h1, h2, h3, h4, h5, h6 {
    font-family: $symbols_font, $title_font;
    a {
        color: var(--default_accent);
        
            border-bottom: none;
        
    }

    a:hover {
        background-color: var(--default_accent);
        color: var(--default_fg);
    }
}

h1 {font-size: 2.0 * $base_font_size * $title_font_mult;}
h2 {font-size: 1.5 * $base_font_size * $title_font_mult;}
h3 {font-size: 1.3 * $base_font_size * $title_font_mult;}
h4 {font-size: 1.0 * $base_font_size * $title_font_mult;}
h5 {font-size: 0.8 * $base_font_size * $title_font_mult;}
h6 {font-size: 0.7 * $base_font_size * $title_font_mult;}

.nerd {
    font-family: $symbols_font, $main_font;
    font-size: 1.2 * $base_font_size;
    margin: 5px;
    display: inline-block;
}

.nerdlink {
    @extend .nerd;
    color: var(--default_fg);
    border-bottom: none;
    position: relative;
    width: 1em;
    height: 1.2em;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    .pseudofont {
        fill: var(--default_fg);
        display: inline;
        position: relative;
        top: 1px;
    }
    span {
        position: absolute;
        top: 1.5em;
        left: 50%;
        transform: translateX(-50%);
        padding: 2px;
        border: 1px solid var(--default_stroke);
        border-radius: 3px;
        color: var(--default_fg);
        display: none;
        background-color: var(--default_bg);
        z-index: 900;
        text-align: center;
    }
    &:hover {
        .pseudofont {
            fill: var(--default_accent);
        }
        color: var(--default_accent);
        text-decoration: none;
        border-bottom: none;
        span {
            display: block;
        }
    }
}

.card {
    display: block;
    padding-bottom: $margin;

    
        background-color: var(--default_hl_bg);
        border-radius: $border_radius;
        box-shadow: $shadow;
        padding: $margin;
        padding-top: 1px;
    

    &.single {
        
            h1 {
                font-size: #{2.2 * $base_font_size * $title_font_mult};
            }
        
    }
}

.postlist {
    display: grid;
    grid-gap: $margin;
    margin-bottom: $margin;
    grid-template-columns: 1fr;
    &.gridView {
        grid-template-columns: repeat(auto-fit, minmax(325px, 1fr));
    }
}

// only hide hr in cards if they are in the article list
// cards in list are divs, cards as in full-blown articles use the article tag

    .postlistitem.card {
        hr { display: none; }
    }


.postlistitem.card div {
    img {
        height: 250px;
        
            min-width: calc(100% + #{2*$margin});
            margin: 0 #{-$margin};
        
        object-fit:
            
                contain
            ;
    }
}

ul {
    li {
        margin: 5px 0;
    }
}

ul.list {
    .date {
        display: inline-block;
        min-width: 100px;
    }
    > li > div {
        display: grid;
        grid-template-columns: 100px auto;
    }
}

#commento {
    margin-top: $margin;
    color: var(--default_fg);
    font-family: $symbols_font, $main_font, monospace;
    textarea, #commento-markdown-help-root {
        background: rgba(255, 255, 255, .05);
        color: var(--default_fg);
        border-radius: 0;
        border: none;
        td, td>*, td>*>* {
            color: var(--default_fg);
            pre {
                font-family: $symbols_font, $extra_mono_font, monospace;
                color: var(--default_dim_fg);
            }
        }
    }
    #commento-submit-button-root {
        background: var(--default_accent);
        color: var(--default_fg);
    }
    .commento-name {
        color: var(--default_dim_fg);
    }
    .commento-sort-policy-buttons {
        a {
            border-bottom: none;
        }
        .commento-sort-policy-button-selected {
            color: var(--default_accent);
        }
    }
    .commento-card {
        border-top: none;
        margin: 25px 0;
    }
    .commento-body {
        >*, *>*, >*>*>*, >*>*>*>*, >*>*>*>*>* {
            color: var(--default_fg);
            font-family: $symbols_font, $main_font, monospace;
        }
    }
}

.search {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    margin: $margin 0;
    .nerdlink {
        display: inline-block;
        cursor: pointer;
        padding: 0 6px;  // appropriately sized tap targets
    }
    #searchbar {
        width: 100%;
        min-width: 150px;
        font-size: 1.3em;
        outline: none;
    }
}

.titleAndSearchContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    #titleContainer {
        flex-grow: 99;
    }
    .search {
        flex-grow: 1;
        &, #searchbar {font-size: 1em;}
    }
}

.featuredCardLink {
    border: none;
    padding: 0;
    .featuredCard {
        @extend .card;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        position: relative;
        height: 250px;
        .contentArea {
            position: absolute;
            bottom: 0;
            padding: $margin;
            &, h2, .date {color: white;}
            h2 {
                margin: 0;
            }
            .date {
                margin: 0;
            }
        }
        
            border-radius: $border_radius;
            padding: 0;
        
    }
}

.loadMoreButton {
    text-align: center;
    font-size: 1.2em;
    font-weight: bold;
    &.buttonLike {
        a {
            @include roundButtonLike;
        }
    }
}

.relatedArticlesContainer {
    margin: $margin 0;
    #relatedArticles .postlistitem {
        @extend .featuredCard;
    }
}

#titleContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: flex-start;
    align-self: flex-start;

    h1 {
        font-weight: normal;
        font-size: 1.6 * $base_font_size * $title_font_mult;
        margin: 0;
        
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            align-items: center;
            > a {
                margin-right: 12px;
            }
        
    }

    img {
        margin-right: ($margin / 2);
        margin-top: 7px;
        align-self: flex-start;
        width: 50px;
        max-height: 100%;
        border-radius: 5px;
    }
}

nav:not(#TableOfContents), #links {
    ul {
        list-style-type: none;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        padding: 0;
        margin: 0;
    }
}

header {
    @include limitWidth;
    padding: $margin;
    margin-bottom: $margin;
    .rightOfLogo {
        width: 100%;
        .titleAndHamburger {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            align-items: flex-start;
            justify-content: space-between;
        }
    }
    nav:not(#TableOfContents) {
        font-size: .9em;
        ul {
            li {
                
                    margin: 5px 2px;
                    margin-right: 5px;
                    &:last-child {
                        margin-right: none;
                    }
                    a {
                        
                            font-family: $symbols_font, $main_font;
                            background-color: var(--default_hl_bg);
                            transition: background-color .15s ease-in-out;
                            &:hover, &:active {
                                background-color: var(--default_accent);
                                color: var(--default_bg);
                            }
                        
                        box-sizing: border-box;
                        color: var(--default_fg);
                        margin: 0;
                        padding: #{$margin/2} $margin;
                    }
                
            }
        }
    }
}

