.articleTagsContainer {
    margin: $margin 0;
}

a.buttonTag {
    @include roundButtonLike;
    font-family: $symbols_font, $mono_font, monospace;
}

.cactus-button, .cactus-login-buttons > button {
    font-family: $main_font;
    padding: 0;
    margin: 5px 0 5px $margin !important;
    @include buttonLike;
    transition-property: border-color, background-color !important;
    transition-duration: .15s;
    transition-timing-function: ease-in-out;
    opacity: 1 !important;
    &[disabled] {
        opacity: .5 !important;
        border-color: transparent !important;
        background-color: transparent !important;
        box-shadow: none;
    }
}

.shareBtn, button {
    @include buttonLike;
    cursor: pointer;
}

input, textarea, textarea.cactus-editor-textarea {
    
        border-radius: 0 !important;
    
    box-sizing: border-box;
    font-family: $symbols_font, $main_font;
    background-color: var(--default_hl_bg) !important;
    color: var(--default_fg);
    max-width: 100%;
    min-width: 10px;
    padding: 12px;
    border: 2px solid transparent !important;
    border-color: transparent;
    transition: border-color .15s ease-in-out !important;
    &:focus, &:active {
        border-color: var(--default_accent) !important;
        outline: none;
    }
}

.cactus-editor-name {
    box-sizing: border-box;
    max-width: 100%;
    min-width: 10px;
}

.cactus-login-form {
   
        border-radius: 0;
    
    
        box-shadow: $shadow;
    
    max-width: 600px;
    min-width: 200px;
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
    transform: none;
    top: #{$margin*2};
    left: 0; right: 0;
    background-color: var(--default_bg) !important;
    color: var(--default_fg) !important;
    z-index: 9999;
    > a {  // "Use a Matrix client" button
        @include buttonLike;
        align-self: flex-end;
        > button {
            background-color: transparent !important;
            border: none !important;
            box-shadow: none !important;
            margin: 0 !important;
            padding: 0 !important;
        }
    }
    .cactus-login-buttons {
        margin: 0;
        justify-content: flex-end;
        > button {
            flex-grow: 0;
            white-space: nowrap;  // otherwise labels go 1 line per word
        }
    }
}

#fediInstanceDialog {
    display: none;
    position: fixed;
    top: 0; bottom: 0; left: 0; right: 0;
    
    .dialog {
        
            box-shadow: $shadow;
        
        box-sizing: border-box;
        position: absolute;
        display: flex; flex-direction: column;
        top: #{$margin*2};
        left: 50%; transform: translateX(-50%);
        z-index: 9999;
        background-color: var(--default_bg);
        width: 450px;
        max-width: 100%;
        padding: $margin;
        
        h1, h2, h3, h4 {
            margin: 0;
        }
        h2 {
            margin-bottom: 24px;
        }
        input {
            display: block;
        }
        .buttons {
            margin: $margin 0;
            display: flex;
            flex-direction: row;
            justify-content: end;
            > * {
                margin: 5px 0 5px $margin;
                display: block;
            }
        }
    }
    .bg {
        z-index: 99;
        position: absolute;
        top: 0; bottom: 0; left: 0; right: 0;
        background-color: rgba(0, 0, 0, .4);
        cursor: pointer;
    }
    &.open {
        display: block;
    }
}


    header {
        #hamburger-menu {
            @extend .nerdlink;
            display: none !important;
        }
        #main-nav-toggler {
            display: none;
        }
    }

    @media only screen and (max-width: 520px) {
        header {
            .titleAndSearchContainer {
                z-index: 9999999 !important;
            }
            #hamburger-menu {
                display: block !important;
                font-size: 1.2em;
                cursor: pointer;
                text-align: center;
                margin: 0;
                padding: ($margin / 4);
                align-self: center;
            }
            nav:not(#TableOfContents) {
                ul#main-nav {
                    &, li, li a {text-shadow: none !important;}
                    display: none;
                    position: absolute;
                    z-index: 99999;
                    right: $margin;
                    padding: 2px 5px;
                    width: 120px;
                    
                    border: 2px solid var(--default_stroke);
                    background-color: var(--default_hl_bg);
                    flex-direction: column;
                    li {
                        border-right: none !important;
                        display: block;
                        text-align: center;
                        margin: 0;
                        a {
                            box-shadow: none;
                            box-sizing: border-box;
                            display: block;
                            width: 100%;
                            padding: 10px;
                            margin: 3px 0;
                        }
                    }
                }
                #main-nav-toggler:checked ~ #main-nav {
                    display: flex;
                }
            }
        }
    }




header {
    .jumbotron { display: none; }
    max-width: 100%;
    > * {
        @include limitWidth;
    }
}
header.jumbotronContainer {
    
    
        &, * {
            text-shadow: 0 0 5px transparentize($light_fg, .3);
        }
    
    > *:first-child {
        padding-top: $margin;
    }
    padding-bottom: 0;
    padding-top: 0;
    .jumbotron { display: flex; }
    
        background:
            linear-gradient(
                transparentize($dark_bg, 0.2),
                transparentize($dark_bg, 0.2)
            ),
            url(/images/me/banner.png);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    
    
        height: 100vh;
        
    
}
.jumbotron {
    width: 100%;
    
        height: 80vh;
    
    flex-wrap: no-wrap;
    text-align: center;
    justify-content: center;
    align-items: center;
    align-content: center;
    
    
        flex-direction: row;
    
    .main_box {
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: center;
        align-items: center;
        flex-basis: 55%;
        flex-grow: 1;
    }
    img {
        flex-basis: 45%;
        flex-grow: 1;
        max-width: 100vw;
        min-width: 200px;
    }
    h2 {
        font-weight: 300;
        &.huge {
            font-size: 4 * $base_font_size * $title_font_mult;
        }
    }
    h2, h3 {
        margin: 12px;
    }
    ul {
        list-style: none;
        padding: 0;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        li {
            margin: 3px;
            a {
                &.huge {
                    font-size: 1.5em;
                }
                
            }
        }
    }
}
@media only screen and (max-width: 520px) {
    .jumbotron {
        flex-wrap: wrap;
        h1.huge {
            font-size: 3 * $base_font_size * $title_font_mult !important;
        }
        h2, h3 {
            margin: 6px;
        }
    }
}
@media only screen and (max-height: 800px) {
    .jumbotron {
        height: auto;
        img {
            max-height: 40%;
        }
    }
}



    $sidebar_width: 350px;

    header {
        #sidebar_nav, .sidebarToc {
            display: none;
        }
        #wide_nav {
            display: block;
        }
    }

    @media only screen and (min-width: 950px) {
        html:not(.home) {
            position: fixed; top: 0; bottom: 0; left: 0; right: 0;
            color: var(--default_sidebar_fg);
            #baseContainer {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
                max-height: 100vh;
                header {
                    margin-left: unset; margin-right: unset;
                    background-color: var(--default_sidebar_bg);
                    max-width: $sidebar_width;
                    min-width: $sidebar_width;
                    height: 100vh;
                    display: block;
                    overflow: auto;
                    margin-bottom: 0;
                    padding: 0 $margin;
                    box-shadow: $shadow;
                    > *:first-child {
                        padding-bottom: $margin;
                        padding-top: $margin;
                    }
                    .rightOfLogo {
                        align-self: center;
                    }
                    #sidebar_nav {
                        display: block;
                        margin: $margin 0;
                        width: 100%;
                        nav:not(#TableOfContents) {
                            font-size: 1.1em;
                            ul#main-nav {
                                flex-direction: column;
                                li {
                                    border-right: none;
                                    a {
                                        display: block;
                                    }
                                }
                            }
                        }
                    }
                    #wide_nav {
                        display: none;
                    }
                    .sidebarToc {
                        display: block;
                        nav#TableOfContents {
                            ul {
                                padding-left: 24px;
                                a:not(:hover) {
                                    border-color: transparent;
                                }
                            }
                        }
                    }
                }
                #contentContainer {
                    flex-grow: 1;
                    overflow-y: auto;
                }
            }
            .articleToc {
                display: none;
            }
        }
    }




// transitions at the end so that loading css doens't cause animations

a {
    transition-property: color, border-color;
    transition-duration: .15s;
    transition-timing-function: ease-in-out;
}

.nerdlink {
    &, .pseudofond {
        transition: all .15s ease-in-out;
    }
}

input {
    transition: .15s border ease-in-out;
}

* {
    line-height: 165%;
}

.admonitionblock {
    table {
        background-color: var(--default_bg);
        tbody {
            tr {
                td {
                    border: 00px solid;
                    /* padding: 10px 15px; */
                }
            }
        }
    }
}

.hdlist {
  table {
  width: 100%;
    tbody {
      tr {
        td {
          border: 0px solid;
          /* width: 200px; */
          /* padding: 10px 15px; */
        }
      }
    }
  }
}

.hdlist1 {
  width: 20%;
  font-weight: bold;
}

.hdlist2 {
  width: 80%;
  padding-left: 3%;
  padding-top: 1px;
  padding-bottom: 1px;
}

.admonitionblock td.content {
  border-left: 1px dashed var(--default_accent);
  padding-left:  20px;
}

.admonitionblock td.icon {

    [class^="fa icon-"] { 
        padding-right: 1px;
        padding-left: 10px;
        font-size: 200%;
        text-shadow: 1px 1px 2px rgba(0,0,0,.5);
        cursor: default;
    }

    .icon-note:before {
        border-right: 10.5px dotted var(--default_bg);
        content:"\f05a";
        color: var(--default_admonition_note);
        font-weight: normal;
        font-style: normal;
    }
    .icon-caution:before {
        content:"\f06d";
        color: var(--default_admonition_caution);
        font-weight: normal;
        font-style: normal;
    }

    .icon-important:before {
        content:"\f06a";
        color: var(--default_admonition_important);
        font-weight: normal;
        font-style: normal;
    }

    .icon-tip:before {
        content:"\f0eb";
        color: var(--default_admonition_tip);
        font-weight: normal;
        font-style: normal;
    }

    .icon-warning:before {
        content:"\f071";
        color: var(--default_admonition_warning);
        font-weight: normal;
        font-style: normal;
    }

}

